const { body } = require("express-validator");

const schedulingValidation = () => {
  return [
    body("title")
      .isString()
      .withMessage({ message: ["Nome de usurio  necessrio!"] }),

    body("start")
      .isString()
      .withMessage({ message: ["Horrio do agendamento  necessrio!"] }),

    body("service")
      .isObject()
      .withMessage({ message: ["Tipo do servio  obrigatrio!"] }),

    body("userEmail")
      .isString()
      .optional()
      .withMessage({ message: ["E-mail de usurio  necessrio!"] }),
  ];
};

module.exports = {
  schedulingValidation,
};
