const { body } = require("express-validator");

const userCreateValidate = () => {
  return [
    body("name")
      .isString()
      .withMessage({message: ["O nome  obrigatrio!"]})
      .isLength({ min: 4 })
      .withMessage({message: ["O nome precisa ter no mnimo 4 caracteres!"]}),

    body("email")
      .isString()
      .withMessage({message: ["O E-mail  obrigatrio!"]})
      .isEmail()
      .withMessage({message: ["Insira um e-mail vlido!"]}),

    body("password")
      .isString()
      .withMessage({message: ["A senha  obrigatria!"]})
      .isLength({ min: 6 })
      .withMessage({message: ["A senha precisa ter no mnimo 6 caracteres!"]}),

    body("confirmPassword")
      .isString()
      .withMessage({message: ["A confirmao de senha  obrigatria!"]})
      .custom((value, { req }) => {
        if (value !== req.body.password) {
          throw new Error({message: ["As senhas no so iguais!"]});
        }
        return true;
      }),
  ];
};

const loginValidate = () => {
  return [
    body("email")
      .isString()
      .withMessage({message: ["O e-mail  obrigatrio!"]})
      .isEmail()
      .withMessage({message: ["Insira um e-mail vlido!"]}),

    body("password")
      .isString()
      .isLength({ min: 6 })
      .withMessage({message: ["A senha  obrigatria!"]})
  ];
};


module.exports = {
  userCreateValidate,
  loginValidate,
};
