const { body } = require("express-validator");

const serviceValidation = () => {
  return [
    body("serviceType")
      .isString()
      .withMessage({message: ["Tipo do servio  obrigatrio!"]}),

    body("serviceName")
      .isString()
      .withMessage({message: ["Nome do servio  obrigattio!"]}),

    body("serviceValue")
      .isInt({min:1})
      .withMessage({message: ["Valor do servio  obrigatrio!"]}),
  ];
};

const updateServiceValidation = () => {
  return [
    body("serviceName")
      .isString()
      .withMessage({message: ["Nome do servio  obrigattio!"]}),

    body("serviceValue")
      .isInt({min:1})
      .withMessage({message: ["Valor do servio  obrigatrio!"]}),
  ];
};

module.exports = {
  serviceValidation,
  updateServiceValidation
};
